.header__navigation {
  display: flex;
  align-items: center;
}

a {
    text-decoration: none;
}
.nav-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    row-gap: 20px;
    column-gap: 40px;
    font-size: 16px;
    font-weight: 500;
    font-family: 'Poppins', sans-serif;
    list-style: none;
}
.nav-list__link {
   color: #fff;
   transition: opacity 0.2s ease-in;
}
.nav-list__link:hover {
  opacity: 0.8;
}
.nav-list__link--active {
   position: relative;
}
.nav-list__link--active::before {
  content: '';
  position: absolute;
  left: 0;
  top: 100%;
  display: block;
  height: 2px;
  width: 100%;
  background-color:  var(--accent);
}

.navbar .mobilenavigation {
  display: none;
}

@media (max-width: 990px) {
  .navbar .navigation {
    display: none;
  }
  .navbar .mobilenavigation {
    display: flex;
    align-items: center;
    
  }
  .navbar .mobilenavigation .hamburger {
    position: absolute;
    right: 5%;
    cursor: pointer;
  }
  .navbar .nav-list {
    display: flex;
    flex-direction: column;
    position: absolute;
    padding: 15px 0;
    top: 50px;
    right: 0;
    width: 100%;
    background: #181818;
    border-top: 2px solid #fff;
  }
  .navbar a:hover {
    border-bottom: none;
    color: #8C1cd1;
  }
  /*hamburger*/
  .hamburger {
    width: 30px;
    height: 20px;
    position: relative;
    margin: 0;
    cursor: pointer;
  }
  .hamburger span {
    position: absolute;
    top: 9px;
    width: 30px;
    height: 2px;
    background-color: #fff;
  }
  .hamburger::before {
    content: '';
    position: absolute;
    top: 0;
    width: 30px;
    height: 2px;
    background-color: #fff;
    
  }
  .hamburger::after {
    content: '';
    position: absolute;
    bottom: 0;
    width: 30px;
    height: 2px;
    background-color: #fff;
  }
